.statistics {
  margin-right: auto;
  margin-left: auto;
  border-radius: 10px;
  padding: 30px;
  background-color: rgb(187, 174, 174);
}

.title {
  text-transform: uppercase;
  text-align: center;
}

.statlist {
  display: flex;
  list-style: none;
  flex-direction: row;
  width: 500px;
  padding: 0;
  margin: 0;
  max-width: 800px;
  margin-left: 200px;
}

.item {
  display: flex;
  flex-direction: column;
  width: 500px;
  text-align: center;
  margin-bottom: 20px;
  border: 1px solid rgba(128, 128, 128, 0.205);
  padding: 20px;
  background-color: yellowgreen;
}

.label {
  margin-bottom: 10px;
  font-size: 15px;
  font-weight: 400;
  color: white;
}

.percentage {
  font-size: 25px;
  font-weight: 700;
  color: white;
}
