.profile {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 40px;
  border-radius: 10px;
  border: 2px solid rgba(78, 76, 76, 0.363);
}

.description {
  text-align: center;
  padding: 25px;
}

.avatar {
  width: 300px;
  height: auto;
  border-radius: 50%;
}

.name {
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 25px;
  font-weight: 700;
}

.tag {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 700;
  color: rgb(117, 114, 114);
}

.location {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 700;
  color: rgb(117, 114, 114);
}

.stats {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  max-width: 800px;
  margin-left: 80px;
}

.item {
  display: flex;
  flex-direction: column;
  background-color: rgb(218, 211, 218);
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
  border: 1px solid rgba(128, 128, 128, 0.205);
}

.label {
  margin-bottom: 10px;
  font-size: 25px;
  font-weight: 400;
  color: rgb(117, 114, 114);
}

.quantity {
  font-size: 25px;
  font-weight: 700;
}
