.friendlist {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 50px;
  width: 100%;
  padding: 20px;
  background-color: white;
}

.item {
  display: flex;
  height: 100px;
  width: 400px;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.avatar {
  width: 70px;
  height: auto;
  margin-right: 15px;
}

.name {
  font-size: 25px;
  font-weight: 400;
}

.status {
  display: flex;
  margin-right: 15px;
  margin-left: 15px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.isOnline {
  composes: status;
  background-color: green;
}
.isOffline {
  composes: status;
  background-color: red;
}
