.transactionhistory {
  margin-right: auto;
  margin-left: auto;
  width: 800px;
  background-color: white;
  margin-bottom: 45px;
  table-layout: fixed;
}

.transactionhistory th {
  font-weight: 400;
  color: white;
  padding: 20px;
  background-color: #0b92aa;
  border: 1px solid white;
}

.transactionhistory td {
  padding: 20px;
  border: 1px solid rgb(139, 136, 136);
  text-align: center;
  color: rgb(94, 91, 91);
}

.transactionhistory tr:nth-child(even) {
  background-color: rgb(158, 215, 228);
}
